body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.navigation {
  display: flex;
  font-size: 2rem;
  background: blue;
  position: fixed;
  width: 100%;
  transition-property: top;
  transition-duration: .6s;
  transition-delay: .2s;
  z-index: 1;
}

.navigation .navigation-empty-first {
  flex: 0.35 1;
}

.navigation .navigation-item {
  margin: 0 2rem;
  text-decoration: none;
  color: red;
}

.navigation .navigation-item .navigation-item-text {
  cursor: pointer;
}

.navigation .navigation-food-menu {
  position: relative;
}

.navigation .navigation-food-menu .navigation-food-menu-list {
  cursor: context-menu;
  position: absolute;
  visibility: hidden;
  right: -6rem;
}

.navigation .navigation-food-menu .navigation-food-menu-list .navigation-food-menu-list-shape {
  height: 20rem;
  width: 15rem;
  background: red;
  -webkit-clip-path: polygon(0 87%, 100% 87%, 100% 13%, 38% 13%, 31% 0, 22% 13%, 0 13%);
          clip-path: polygon(0 87%, 100% 87%, 100% 13%, 38% 13%, 31% 0, 22% 13%, 0 13%);
}

.navigation .navigation-food-menu .navigation-food-menu-list .navigation-food-menu-list-shape .navigation-categories-list {
  padding-top: 2.4rem;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.navigation .navigation-food-menu:hover .navigation-food-menu-list {
  visibility: visible;
}

.navigation-little-visible {
  top: 0;
  opacity: 0.3;
}

.navigation-hidden {
  opacity: 0.3;
  top: -5rem;
}
