}

.home .reservation .reservation-entries .reservation-entry .reservation-entry-dropdown .reservation-entry-dropdown-item:hover {
  background: #868a8d;
  border-color: #9371d3;
}

.home .reservation .reservation-entries .reservation-entry .reservation-entry-dropdown .simplebar-vertical {
  background: gray;
  visibility: visible;
}

.home .reservation .reservation-entries .reservation-entry .reservation-entry-dropdown .simplebar-vertical {
  visibility: hidden;
}

.home .reservation .reservation-entries .reservation-entry .reservation-entry-dropdown-hidden {
