}

.reservation .reservation-entries .reservation-entry .react-calendar {
  position: absolute;
}

.home .reservation .reservation-entries-book-button {
  display: flex;
  justify-content: center;
}

.home .reservation .reservation-entries-book-button .reservation-entries {
  display: flex;
  justify-content: center;
}

.home .reservation .reservation-entries-book-button .reservation-entries .reservation-entry {
  margin: 2rem;
  width: 10rem;
}

.home .reservation .reservation-entries-book-button .reservation-entries .reservation-entry .reservation-entry-text-arrow {
  cursor: pointer;
  display: flex;
  justify-content: space-around;
  background: grey;
  height: 2.5rem;
  width: 100%;
  border-bottom: 1px solid black;
}

.home .reservation .reservation-entries-book-button .reservation-entries .reservation-entry .reservation-entry-text-arrow .reservation-entry-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home .reservation .reservation-entries-book-button .reservation-entries .reservation-entry .reservation-entry-text-arrow .arrow-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home .reservation .reservation-entries-book-button .reservation-entries .reservation-entry .reservation-entry-text-arrow .arrow-wrapper .arrow {
  height: .6rem;
  width: .6rem;
  border-bottom: 2px solid red;
  border-right: 2px solid red;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.home .reservation .reservation-entries-book-button .reservation-entries .reservation-entry .reservation-entry-text-arrow .arrow-wrapper .arrow-down {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  margin-top: 0;
}

.home .reservation .reservation-entries-book-button .reservation-entries .reservation-entry .reservation-entry-text-arrow .arrow-wrapper .arrow-up {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
  margin-top: 0.6rem;
}

.home .reservation .reservation-book-button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin: 2rem;
  cursor: pointer;
  font-size: 1.4rem;
  background: #ceadad;
  height: 3rem;
  padding: 0.1rem 0.4rem;
  border-radius: 3px;
}

.home .reservation {
  position: relative;
}

.home .reservation .booking-complete {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: white;
  height: 20rem;
  width: 20rem;
}

.home .reservation .booking-complete .booking-complete-exit {
  padding-right: 1.5px;
  position: absolute;
  top: 0;
  right: 0;
  height: 30px;
  width: 30px;
  background: grey;
  cursor: pointer;
}

.home .reservation .booking-complete .booking-complete-exit:before, .home .reservation .booking-complete .booking-complete-exit:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 30px;
  width: 3px;
  background: black;
}

.home .reservation .booking-complete .booking-complete-exit:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.home .reservation .booking-complete .booking-complete-exit:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.home .reservation .booking-complete .close {
  cursor: pointer;
}

.home .reservation .booking-complete-visible {
  visibility: visible;
}

.home .reservation .booking-complete-hidden {
  visibility: hidden;
}

.home .reservation {
  background-image: url(/restaurant3578/static/media/reserve-background.812d879c.jpg);
  background-attachment: fixed;
}

.home .reservation .reservation-title {
  color: white;
}
